import { addDays, format } from "date-fns";
import orderBy from "lodash/orderBy";
import { action, computed, makeAutoObservable, runInAction } from "mobx";
import { Platform } from "react-native";
import { db, storage } from "../firebase/config";
import TaskItem from "../models/TaskItem";
import "../utils/Extensions";

class StorageStore {

  loading = false;  

  constructor () {
    console.log("Initialize TaskStore...");
    makeAutoObservable(this);
  }

  addAttachments = async(taskItem,file,cbAction) => {
    this.loading = true;
    let path = this.getPlatformPath(file).value;
    let name = this.getFileName(file.fileName,path);
    const filePath = "attachFiles/" + name;    
    runInAction( async()=> {
      this.loading = false;
      const response = await fetch(path);
      const blob = await response.blob();
      
      const ref = storage.ref().child(filePath);
      const taskUpload = await ref.put(blob);

      const url = await ref.getDownloadURL();
      console.log(url);
      const fileAttach = {value: path, ref: filePath, url: url};
      taskItem.attachments.push(fileAttach);
      if (cbAction) cbAction();
    })
  }

  getFileName = (name, path) => {
    if (name != null) { return name; }

    if (Platform.OS === "ios") {
        path = "~" + path.substring(path.indexOf("/Documents"));
    }
    if (Platform.OS == "web") {
      const date = new Date();
      return format(date, "yyMMdd_") + Date.now() ;
    }
    return path.split("/").pop();
  }

  /**
   * Get platform specific value from response
   */
  getPlatformPath = ({ path, uri }) => {
      return Platform.select({
          android: {value: path },
          web: {value: uri},
          ios: {value: uri }
      });
  }

  getPlatformURI = (imagePath) => {
      let imgSource = imagePath;
      if (isNaN(imagePath)) {
          imgSource = { uri: this.state.imagePath };
          if (Platform.OS == "android") {
              imgSource.uri = "file:///" + imgSource.uri;
          }
      }
      return imgSource;
  }

}

export default StorageStore;
