import { Body, Button, ListItem, Right, Switch, Spinner } from 'native-base';
import { Text } from "native-base";
import React, { useState, useEffect } from "react";
import { Image, Platform, StyleSheet, TouchableWithoutFeedback, View } from "react-native";
import { observer } from "mobx-react-lite";
import * as ImagePicker from "expo-image-picker";
import { useNavigation, useRoute, useIsFocused  } from "@react-navigation/native";
import { useStores } from "../stores/RootStore";
import TaskItem from '../models/TaskItem';
import { FontAwesome } from '@expo/vector-icons';
import { TextInput } from 'react-native-gesture-handler';
import CommentList from './CommentList';


const TaskListItem = (props) => {
  const taskItem: TaskItem = props.taskItem;
  const navigation = useNavigation();
  const route = useRoute();
  const {taskStore, storageStore} = useStores();

  const [showComment, setShowComment] = useState(false);

  const cbAttachAction = ()=> {
    taskStore.saveSelectList(props.user,props.date);
  }

  const showImagePicker = async (taskItem) => {
    // ask the user for the permission to access the media library
    const permissionResult = await ImagePicker.requestMediaLibraryPermissionsAsync();

    if (permissionResult.granted === false) {
      alert("You've refused to allow this appp to access your photos!");
      return;
    }

    const result = await ImagePicker.launchImageLibraryAsync();

    if (result.cancelled) {
      console.log("User cancelled image picker");
    } else {
      storageStore.addAttachments(taskItem, result, cbAttachAction);
      if (props.onAddFile){
        props.onAddFile();
      }
    }
  };
  
  
  const takePicture = (taskItem) => {
    navigation.navigate("CameraScreen",{ sourceScreen: "HomeScreen", sourceData: {id: taskItem.id, user: props.user, taskItem: taskItem } });
  };
  const attachments = (taskItem) => {
    return taskItem.attachments.map(attachItem => (              
      <Image source={{uri: attachItem.value?? attachItem.url}} style={{width: 50,height: 50, marginLeft: 20}}></Image>
    ));
  };

  return (
    <ListItem key={taskItem.id} style={{ backgroundColor: taskItem.isSelected? "#cde1f9": "#fcfcfc"}}>
      <Body style={{flexDirection: "column"}}>
        <View style={{flexDirection: "row"}}>
          <View>
            <TouchableWithoutFeedback onPress={() => props.onSelectItem(taskItem)}>
              <View>
                <Text style={styles.titleText}>{taskItem.id}. {taskItem.task}</Text>
                <Text style={styles.baseText}>Mục: {taskItem.cat} </Text>
                { taskItem.weekCount > 0 ?
                <Text style={{fontSize: 13,color: taskItem.weekCount > taskItem.freqPerWeek? "red": ""}}>Đã chọn: {taskItem.weekCount} lần
                  (Được chọn {taskItem.freqPerWeek} lần) </Text>
                : <Text style={{fontSize: 13}}>Được chọn {taskItem.freqPerWeek} lần </Text> }
              </View>
            </TouchableWithoutFeedback>
          </View>
          <View style={{flexDirection: "row"}}>
          {
            attachments(taskItem)
          }
          {
            storageStore.loading && route.params?.id == taskItem.id && <View><Spinner/><Text>Đang tải ảnh lên... </Text></View>
          }
          </View>
        </View>
        <View>
          {
            showComment && <CommentList item={taskItem} user={props.user} date={props.date}></CommentList>
          }
        </View>
      </Body>
      { taskItem.isSelected &&
      <Right style={{width:250, minWidth: 200, maxWidth:300, flexDirection: "row", justifyContent: "space-evenly", alignContent: "flex-start", alignSelf: "flex-start"}}>
          <View style={styles.taskButton}>
            <Switch value={taskItem.isCompleted} onValueChange={(e)=> { props.onDoneItem(taskItem);}}  />
          </View>
          <Button style={styles.taskButton} transparent onPress={() => setShowComment(!showComment)}>
              <FontAwesome name="comment" style={{fontSize:25}} color={taskItem.comments.length > 0 ? "red": "rgb(63, 81, 181)"} />
              { taskItem.comments.length > 0 && <Text style={{fontSize:14, fontWeight: "bold", color: "white", position: "absolute"}}>{taskItem.comments.length}</Text>}
          </Button>
          <Button style={styles.taskButton} transparent onPress={() => takePicture(taskItem)} >
              <FontAwesome style={{fontSize:25}} name="camera" color="rgb(63, 81, 181)" />
          </Button>
          <Button style={styles.taskButton} transparent onPress={() => showImagePicker(taskItem)}>
              <FontAwesome style={{fontSize:25}} name="cloud-upload" color="rgb(63, 81, 181)" />
          </Button>
      </Right>
      }
    </ListItem>
  );
};

const styles = StyleSheet.create({
  baseText: {
    fontSize: 11,
  },
  titleText: {
    fontSize: 15,
    fontWeight: "bold"
  },
  input: {
    height: 40,
    maxWidth: 500,
    marginLeft: 20,
    fontSize: 18,
    borderWidth: 1,
    borderColor: "rgb(201, 201, 201)"
  },
  taskButton: {
    flex: 1,
    justifyContent: "center",
    alignContent: "center",
    alignSelf: "center"
  }
});


const taskList = observer(TaskListItem);
export default taskList;